<template>
	<div>
		<h1 style="text-align: center;padding: 25px;">职业性格测评报告</h1>
		<div  class="title" id="personality-test-score">
			<span >职业性格得分</span>
		</div>
        <div class="container" style="height: 320px;">
            <score-bar :data="data"></score-bar>
        </div>

		<div  class="title" style="margin: 30px; 0" id="personality-test-analysis">
			<span >职业性格指标分析</span>
		</div>
		<div class="container">
            <analysis-table :data="data"></analysis-table>
		</div>

		<div  class="title" style="margin: 40px auto;" id="personality-test-match">
			<span>匹配职位</span>
		</div>
		<div class="container flex-container" style="justify-content: space-between">
			<career-card titleBackground="#87cfd9" title="算法工程师"></career-card>
			<career-card titleBackground="#a7cced" title="投资顾问"></career-card>
			<career-card titleBackground="#52738f" title="数据挖掘工程师"></career-card>			
		</div>
		<anthor :config="config"></anthor>	
	</div>
</template>

<script>
import careerCard from '../components/career-card';
import anthor from '../components/anthor';
import scoreBar from '../components/score-bar';
import analysisTable from '../components/analysis-table' 
import http from '../api/http';
export default {
	components: {
		careerCard,
        anthor,
        scoreBar,
        analysisTable
	},
	mounted() {
        window.scrollTo(0, 0);		
		this.getData();
	},
	data() {
		return {
			config: [
				{
					name: '职业能力雷达图',
					id: 'personality-test-score'
				},
				{
					name: '职业性格指标分析',
					id: 'personality-test-analysis'
				},
				{
					name: '匹配职位',
					id: 'personality-test-match'
				}
            ],
            data:[]
		}
	},
	methods: {
		getData() {
			this.getScoreData()
		},
		getScoreData() {
			
			let data = this.$store.state.data ||  sessionStorage.getItem('save_postData')
			if(data) {
				this.data =JSON.parse(data);
			} else {
                this.$router.push({name: 'Test'})
			}
		}
	}, 
	
}
</script>

<style>

.flex-container {
	display: flex;
	justify-content: center;
	flex-wrap: nowrap;
	align-items: center;
}
</style>





