<template>
    <div>
        <div class="container">
            <div  class="title marginTop">
                <span >职业能力测评</span>
            </div>
            <div class="part">{{btnText}}</div>
            <template v-for="(item, i) in radioListData">
                    <radio-item :key="i" :data="item" :index='i' @select="select"></radio-item>
            </template>
            <div style="text-align: center;">
                <el-button type="primary" @click="clickHandle">提交</el-button>
            </div>
        </div>
    
<!--左边的跳转栏目-->
        <div class="anthor" style="position:fixed; top: 50px;">
            <div class="item short-width" :class="{'selected': partPageIndex === 1}" @click="testPartChange(1)">
                PART A
            </div>
            <!--
            <div class="item short-width" :class="{'selected': partPageIndex === 2}" @click="testPartChange(2)">
                PART B
            </div>-->
        </div>
    </div>
</template>
<script>
import radioItem from '../components/radio-item'
import http from '../api/http';

export default {
    components: {
        radioItem
    },
    data() {
        return {
            partPageIndex: 1,
            radioListData: [],
            postdata: [],
        }
    },
    mounted() {
        this.getTestData()
    },
    methods: {
        getTestData() {
            http.get('/index/character').then((res) => {
				if(res.status === 200 && res.data && res.data.length > 0) {
                    this.radioListData = res.data;
                    this.postdata = res.data.map((item) => undefined);
				}
			})
        },
        testPartChange(num) {
            this.partPageIndex = num;
        },
        clickHandle() {
               let data = JSON.stringify(this.postdata.slice(0))
               console.log(data);
                http.post('/index/character/submit', {"data": data})
                    .then((res) => {
                        if(res.status === 200 && res.data) {
                            let data = JSON.stringify(res.data)
                            this.$store.dispatch('save_postData', data)
                            sessionStorage.setItem('save_postData',data);
                            this.$router.push({name: 'CareerMatch'})
                        }
                    })
        },
        select(index, orderNumber, value) {
            console.log('index=',index, '  orderNumber=', orderNumber, ' value', value)
            console.log(this.postdata, 'postData');
            this.postdata[index] = {
                order_number: orderNumber,
                question: value
            }
        }
    },
    computed: {
        btnText() {
            return this.partPageIndex === 1 ? 'PART A' : 'PART B'
        },
    },
}
</script>
<style lang="css">
.marginTop {
    margin-top: 30px;
}
.part {
    font-size: 16px;
    font-weight: 16px;
    padding: 10px;
    text-align: center;
}
.anthor .short-width {
    width: 60px;
}

.anthor .item {
    font-size: 14px;
    display: block;
    line-height: 36px;
    background: #f2f2f2;
    padding: 6px 20px;
    border-radius: 0 4px 4px 0;
    margin-bottom: 10px;
    cursor: pointer;
}
.item.selected {
    background: #99ccff;
}
</style>



