import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home.vue'
import Test from '../pages/test.vue'
import CareerMatch from '../pages/career-match.vue'
import PersonalityTest from '../pages/personality-test.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    {
      path: '/career-match',
      name: 'CareerMatch',
      component: CareerMatch
    },
    {
      path: '/personality-test',
      name: 'PersonalityTest',
      component: PersonalityTest
    }
  ]
})



