<template>
  <el-table :data="tableData" >
    <el-table-column
      type="index"
      width="50">
    </el-table-column>
    <el-table-column
      prop="name"
      label="指标"
      width="150">
    </el-table-column>
      <el-table-column
        prop="totalScore"
        label="得分"
        width="80">
      </el-table-column>
      <el-table-column label="低分" align="center">
        <el-table-column prop="lowScoreCount" label="分值" width="80">
        </el-table-column>
        <el-table-column prop="lowTip" label="提示">
        </el-table-column>
      </el-table-column>
        <el-table-column label="高分" align="center">
        <el-table-column prop="highScoreCount" label="分值" width="80">
        </el-table-column>
        <el-table-column prop="highTip" label="提示">
        </el-table-column>
      </el-table-column>
  </el-table>
</template>
</template>

<script>
    export default {
        data() {
            return {
                tableData: [
                    {
                        name: '适应性与焦虑型',
                        totalScore: 3.3,
                        lowScoreCount: 1,
                        lowTip: '适应性， 极端得分可能进取型弱',
                        highScoreCount: 1,
                        highTip: '适应性， 极端得分可能进取型弱',
                    },
                                        {
                        name: '适应性与焦虑型',
                        totalScore: 3.3,
                        lowScoreCount: 1,
                        lowTip: '适应性， 极端得分可能进取型弱',
                        highScoreCount: 1,
                        highTip: '适应性， 极端得分可能进取型弱',
                    },
                                        {
                        name: '适应性与焦虑型',
                        totalScore: 3.3,
                        lowScoreCount: 1,
                        lowTip: '适应性， 极端得分可能进取型弱',
                        highScoreCount: 1,
                        highTip: '适应性， 极端得分可能进取型弱',
                    }
                ]
            }
        }
    }
</script>
<style>
</style>



