<template>
    <div class="anthor" style="position:fixed; top: 50px;">
        <div v-for="(item, index) in config" 
            @click="scroll(item.id, index)"
            class="item" 
            :key="index" 
            :href=" $route.path + '#' + item.id"
            :class="{'selected': selectedIndex === index}">
            {{item.name}}
        </div>
	</div>
</template>

<script>
    export default {
        props: ['config'],
        data() {
            return {
                selectedIndex: null, // 选中的下标数据
                timer: null
            }
        },
        methods: {
            scroll(id, index) {
                let self = this
                let target = this.idToScrollTop(id);
                let pageTop = document.body.scrollTop || document.documentElement.scrollTop;
                window.scrollTo(0, target);
            },
            // id对应的scrollTop的大小
            idToScrollTop(id) {
                let refreshScrollTop = document.getElementById('app').getBoundingClientRect().top
                let rect = document.getElementById(id).getBoundingClientRect();
                return rect.top - refreshScrollTop > 20 ? rect.top - refreshScrollTop  - 20 : rect.top - refreshScrollTop 
            },
            position() {
                const scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
                for (let index = 1; index < this.scrollTopArray.length; index++) {
                    let item = this.scrollTopArray[index];
                    if(scrollTop >= this.scrollTopArray[index - 1] && scrollTop < this.scrollTopArray[index]) {
                        this.selectedIndex = index - 1;
                    } else if (scrollTop > this.scrollTopArray[index]){
                        this.selectedIndex = index;
                    }
                }
                if(scrollTop + this.clientHeight >= this.pageHeight) {
                    this.selectedIndex = this.scrollTopArray.length - 1;
                }
            }
        },
        computed: {
            scrollTopArray() {
                let arr =  this.config.map(item => {
                    return this.idToScrollTop(item.id)
                });
                return arr;
            },
            clientHeight() {
                return document.documentElement.clientHeight || document.body.clientHeight;
            },
            pageHeight() {
                return document.documentElement.scrollHeight || document.body.scrollHeight;
            },
            scrollTop() {
                return document.body.scrollTop || document.documentElement.scrollTop;
            }
        },
        mounted() {
            this.position()
            if(this.config && this.config.length > 0) {
                window.addEventListener('scroll', () => {
                    this.position()
                })
            }
        },
    }
</script>
<style>

.anthor .item {
    font-size: 14px;
    display: block;
    line-height: 36px;
    width: 120px;
    background: #f2f2f2;
    padding: 6px 20px;
    border-radius: 0 4px 4px 0;
    margin-bottom: 10px;
    cursor: pointer;
}
.item.selected {
    background: #99ccff;
}

</style>



