<template>
    <div class='bar' ref="bar"></div>
</template>

<script>

    export default {
        props: {
            data: {
                type: Array
            }
        },
        data() {
            return {
                radarChart: null,
            };
        },
        methods: {
            drawRadar() {
                if (this.$refs.bar) {
                    let barChart = this.$echarts.init(this.$refs.bar);
                    let xAxisData = this.data.map((item) => item.type);
                    console.log(this.data)
                    console.log(xAxisData, 'xAxisData   ')
                    let data = this.data.map((item) => item.score)
                    let option = {
                        color: ['#3398DB'],
                        tooltip : {
                            trigger: 'axis',
                            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                                type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                            }
                        },
                        grid: {
                            left: '3%',
                            right: '4%',
                            bottom: '3%',
                            containLabel: true
                        },
                        xAxis : [
                            {
                                type : 'category',
                                data : xAxisData,
                                axisTick: {
                                    alignWithLabel: true
                                }
                            }
                        ],
                        yAxis : [
                            {
                                type : 'value',
                                max: 100
                            }
                        ],
                        series : [
                            {
                                name:'直接访问',
                                type:'bar',
                                barWidth: '60%',
                                label: {
                                    normal: {
                                        show: true,
                                        position: 'top'
                                    }
                                },
                                data:data
                            }
                        ]
                    };
                    barChart.setOption(option)
                }
            }
        },
        created() {
            this.$nextTick(() => {
                this.drawRadar();
            });
        },
        watch: {
            data() {
                this.drawRadar();
            }
        },
    };
</script>
<style>
.bar {
    width: 100%;
    height: 100%;
}
</style>



