<template>
    <div class='bar' ref="bar"></div>
</template>

<script>

    export default {
        props: {
            data: {
                type: Array
            }
        },
        data() {
            return {
                radarChart: null,
            };
        },
        methods: {
            drawRadar() {
                if (this.$refs.bar) {
                    let barChart = this.$echarts.init(this.$refs.bar);
                    let option = {
                        tooltip: {
                            trigger: 'axis',
                            axisPointer: {
                                type: 'shadow'
                            }
                        },
                        legend: {
                            data: ['能力得分', '职位最高分']
                        },
                        grid: {
                            left: 80
                        },
                        toolbox: {
                            show: true
                        },
                        xAxis: {
                            type: 'value',
                            name: 'Days',
                            axisLabel: {
                                formatter: '{value}'
                            }
                        },
                        yAxis: {
                            type: 'category',
                            inverse: true,
                            data: ['xx能力1', 'xx能力2', 'xx能力3', 'xx能力4', 'xx能力5'],
                        },
                        series: [
                            {
                                name: '能力得分',
                                type: 'bar',
                                barWidth: '20%',
                                data: [65, 70, 30,60,18],
                                itemStyle: {
                                color: '#aaa'  
                                },
                                label: {
                                    normal: {
                                        show: true,
                                    }
                                }
                            },
                            {
                                name: '职位最高分',
                                type: 'bar',
                                barGap: "30%",
                                itemStyle: {
                                color: '#a0a25a'  
                                },
                                label: {
                                    normal: {
                                        show: true,
                                    }
                                },
                                data: [96, 85, 93,100,98]
                            }
                        ]
                    };
                    barChart.setOption(option)
                }
            },
        },
        created() {
            this.$nextTick(() => {
                console.log('create');
                this.drawRadar();
            });
        },
    };
</script>
<style>
.bar {
    width: 100%;
    height: 100%;
}
</style>



