<template>
    <div>
    <div style="overflow: hidden">
        <table class="analysis-table" cellspacing="0" border="0">
            <thead>
                <tr>
                    <th colspan="1" rowspan="2" width="80">
                        <div class="cell">16PF指标</div>
                    </th>
                    <th colspan="1" rowspan="2" width="280">
                        <div class="cell">低分者典型特征</div>
                    </th>
                    <th colspan="9" rowspan="1">
                        <div class="cell">百分位数</div>
                    </th>
                    <th colspan="1" rowspan="2" width="280">
                        <div class="cell">高分者典型特征</div>
                    </th>
                </tr>
                <tr>
                    <th  colspan="1" rowspan="1">10</th>
                    <th  colspan="1" rowspan="1">20</th>
                    <th  colspan="1" rowspan="1">30</th>
                    <th  colspan="1" rowspan="1">40</th>
                    <th  colspan="1" rowspan="1">50</th>
                    <th  colspan="1" rowspan="1">60</th>
                    <th  colspan="1" rowspan="1">70</th>
                    <th  colspan="1" rowspan="1">80</th>
                    <th  colspan="1" rowspan="1">90</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, i) in tableData" :key="i">
                    <td>{{item.name}}</td>
                    <td>
                        <div class="cell" v-if="item.lowScore.length < 24">{{item.lowScore}}</div>
                        <el-tooltip class="item" effect="dark" :content="item.lowScore" placement="top-end" v-else>
                            <div class="cell">{{item.lowScore}}</div>
                        </el-tooltip>
                    </td>
                    <td colspan="9" style="padding: 0">
                        <div class="percentage-cell" :style="{width: item.percentile + '%'}">
                            {{item.percentile}}
                        </div>
                    </td>
                    <td>
                        <div class="cell" v-if="item.highScore.length < 24">{{item.highScore}}</div>
                        <el-tooltip class="item" effect="dark" :content="item.highScore" placement="top-end" v-else>
                            <div class="cell">{{item.highScore}}</div>
                        </el-tooltip>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
    </div>
</template>
</template>

<script>
    export default {
        data() {
            return {
            }
        },
        props: ['data'],
        computed: {
            tableData() {
                return this.data.map((item) => {
                    return {
                        name: item.type,
                        highScore: '分数高时的表现',
                        lowScore: '分数低时的表现',
                        percentile: item.score
                    }
                })
            }
        },
    }
</script>
<style scoped>
.analysis-table {
    width: 100%;
    table-layout:fixed;
    border-collapse:separate;
    border-left: 1px solid #EBEEF5;
    border-top:  1px solid #EBEEF5;
}
.analysis-table thead {
    color: #909399;
    font-weight: 500
}
.analysis-table tr {
    display: table-row;
}
.analysis-table th {
    white-space:nowrap;
    overflow: hidden;
    user-select: none;
    padding: 12px 8px;
    box-sizing:border-box;
    vertical-align: middle;
    position: relative;
    text-align: left;
    background: #f5f7fa;
    border-right: 1px solid #EBEEF5;
    border-bottom:  1px solid #EBEEF5;
}
 .analysis-table td {
    border-right: 1px solid #EBEEF5;
    border-bottom:  1px solid #EBEEF5;
    padding: 12px 8px;
    color: #606266;
    height: 44px;
    max-height: 44px;
    box-sizing: border-box;

}
.analysis-table .cell {
    white-space:nowrap; 
    overflow:hidden; 
    text-overflow:ellipsis;
}
 .analysis-table .percentage-cell {
     height: 44px;
     background: #aaa;
     text-align: right;
     color: white;
     padding-right: 10px;
     vertical-align: middle;
     line-height: 44px;
}
</style>



