import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const state = {   //要设置的全局访问的state对象
	data: null,
	number: 0,
	//要设置的初始属性值
};
const getters = {
	numAdd(state) {  //承载变化的showFooter的值
		return state.number + 1
	}
};
const mutations = {
	save_postData(state, newValue) { //同上，这里面的参数除了state之外还传了需要增加的值sum
		state.data = newValue
	}
};
const actions = {
	save_postData({ commit }, newValue) {  //自定义触发mutations里函数的方法，context与store 实例具有相同方法和属性
		commit('save_postData', newValue);
	}
};
const store = new Vuex.Store({
	state,
	getters,
	mutations,
	actions
});
export default store;


