<template>
    <div class="career-card" >
		<div class="banner" :style="{background:titleBackground }">
			{{title}}
		</div>
		<div class="content">
			<h4>职业描述</h4>
				<p>{{jobDescription}}</p>
			<h4>工作职责</h4>
				<p>{{professionalDuties}}</p>
		</div>
		<el-button class="btn" type="primary">联系该职业导师</el-button>
	</div>
</template>

<script>
    export default {
        props: {
            title: {
                type: String,
                default: '数据挖掘工程师'
            },
            titleBackground: {
                type: String,
                default: '#1277af'
            },
            jobDescription : {
                type:String,
                default: '这里是职业描述的内容，这里是职业描述的内容这里是职业描述的内容这里是职业描述的内容'
            },
            professionalDuties: {
                type:String,
                default: '这里是工作职责的内容，这里是工作职责的内容，'
            }
        },
        data() {
            return {}
        }
    }
</script>
<style scoped>
.career-card {
	text-align: center;
    width: 280px;;
	height: 452px;
	box-sizing: border-box;
	padding: 10px;
	background: #f5f5f5;
	position: relative;
}

.career-card .banner {
	background: #1277af;
	text-align: center;
	font-size: 18px;
	color:#fff;
	font-weight: bold;
	height: 180px;
	line-height: 180px;
}

.career-card .content h4 {
	text-align: center;
	margin-top: 15px;
}
.career-card .content p {
    text-align: left;
}
.career-card .btn {
	position: absolute;
	bottom: 20px;
	left: 50%;
	padding: 8px 12px;
	transform: translateX(-50%)
}
</style>



