<template>
    <div class="radio-box">
        <p>{{index + 1}}. {{data.question}}</p>
          <el-radio-group v-model="radio">
            <el-radio :label="1">A: {{data.A}}</el-radio>
            <el-radio :label="2">B: {{data.B}}</el-radio>
            <el-radio :label="3">C: {{data.C}}</el-radio>
        </el-radio-group>
    </div>
</template>

<script>
export default {
    data() {
        return {
            radio: null, 
        }
    },
    props: ['data', 'index'],
    watch: {
        radio() {
            this.$emit('select', this.index, this.data.order_number, this.radio)
        }
    },
}
</script>

<style scoped>
.radio-box {
    margin: 20px 0;
}
.radio-box p {
    margin-bottom: 10px;
    color:#606266;
    font-size: 16px;
}
</style>



