<template>
    <el-row class="title-container">
        <el-col :span="8" style="min-height:10px">
            <div class="left" v-if="isLeft">
                <div class="width-40"></div>
                <div class="width-70"></div>
                <div class="width-80"></div>
                <div class="width-60"></div>
            </div>
        </el-col>
        <el-col :span="8">
            <div class="content">
                <div class="text">{{titleText}}</div>
                <ul class="span-box">
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>
            </div>
        </el-col>
        <el-col :span="8" style="min-height:10px">
            <div class="right" v-if="!isLeft">
                <div class="width-40"></div>
                <div class="width-70"></div>
                <div class="width-80"></div>
                <div class="width-60"></div>
            </div>
        </el-col>
    </el-row>
</template>
<script>
export default {
    name: 'title-name',
    props: ['isLeft', 'titleText'],
}
</script>
<style scoped>
.title-container {
    margin: 30px 0;
    height: 50px;
}
.content {
    text-align: center;
}
.content .text{
    font-size: 18px;
    font-weight: 600;
    line-height: 40px;
}
.content .span-box {
    overflow: hidden;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}
.content .span-box li{
    float: left;
    list-style: none;
    height: 5px;
    width: 5px;
    margin: 0 1px;
    background: blue;
}
.right, .left {
    overflow: hidden;
    padding: 5px 0;
} 
.right div{
    height: 6px;
    margin: 2px 0;
    float: right;
    border-radius: 4px 0 0 4px; 
    background: #d3e6f6;
}
.left div{
    height: 6px;
    margin: 2px 0;
    float: left;
    border-radius: 0 4px 4px 0; 
    background: #d3e6f6;
}
.width-40 {
    width: 40%;
}
.width-60 {
    width: 60%;
}
.width-70 {
    width: 70%;
}
.width-80 {
    width: 80%;
}
</style>



