<template>
    <div class="container detail-item-container">
        <div class="detail-item__title">XXXX能力</div>
        <div class="flex-container">
            <div class="bar-chart-box">
                <div class="bar-chart" ref="barChart"></div>
            </div>
            <div class="desc-box">
                <h3>XXXX</h3>
                <p>文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxxx</p>
                <h3>XXXX</h3>
                <p>文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容x文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxx文案内容xxxxxxxxxxxxx</p>
            </div>
        </div>
    </div>
</template>
</template>

<script>
    export default {
        data() {
            return {
                barChart: null,
            }
        },
        mounted() {
            this.drawBar()
        },
        methods: {
            drawBar() {
                if (this.$refs.barChart) {
                    let barChart = this.$echarts.init(this.$refs.barChart);
                    let option = {
                        color: ['#3398DB'],
                        tooltip : {
                            trigger: 'axis',
                            axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                                type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                            }
                        },
                        grid: {
                            left: '3%',
                            right: '4%',
                            bottom: '3%',
                            containLabel: true
                        },
                        xAxis : [
                            {
                                type : 'category',
                                data : [ 'xx能力', 'xx能力', 'xx能力', 'xx能力', 'xx能力'],
                                axisTick: {
                                    alignWithLabel: true
                                }
                            }
                        ],
                        yAxis : [
                            {
                                show: false,
                                type : 'value'
                            }
                        ],
                        series : [
                            {
                                name:'职业能力测评',
                                type:'bar',
                                barWidth: '60%',
                                data:[
                                    {
                                        value: 35,
                                        itemStyle: {
                                            color: '#00aaeb'
                                        }
                                    },
                                    {
                                        value: 130,
                                        itemStyle: {
                                            color: '#fad5d4'
                                        }
                                    },
                                    {
                                        value: 81,
                                        itemStyle: {
                                            color: '#64d7ff'
                                        }
                                    },
                                    {
                                        value: 480,
                                        itemStyle: {
                                            color: '#e65f5f'
                                        }
                                    },
                                    {
                                        value: 350,
                                        itemStyle: {
                                            color: '#003298'
                                        }
                                    }
                                ]
                            }
                        ]
                    };
                    barChart.setOption(option)
                }
            },
        },
    }
</script>
<style>
.detail-item-container {
}
.detail-item__title {
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    font-weight: bold;
    padding: 20px 0 25px;
}

.bar-chart-box {
    width: 280px;
    height: 220px;
}
.bar-chart-box .bar-chart {
    height: 100%;
    width: 100%;
}
.desc-box {
    flex: 1;
    height: 220px;
    margin-left: 30px;
    text-align: left;
}
.desc-box h3{
    font-size: 20px;
    line-height: 38px;
    display: inline-block;
    padding-right: 80px;
    border-bottom: 1px solid #ccc;
}
.desc-box p {
    padding: 20px 0;
}
</style>



