<template>
	<div>
		<h1 style="text-align: center;padding: 25px;">职业匹配测评报告</h1>
		<div  class="title" id="career-match-radar">
			<span >职业能力雷达图</span>
		</div>
		<div class="container flex-container feature-container" style="margin-bottom: 40px;">
			<div class="radar-box" id='radarChart'>
			</div>
			<div class="right-content">
				<div>xxx能力 
					<span style="color:#06cdcd">55分</span>
					<p>文案111文案11文案11文案11文案11</p>
				</div>
				<div>xxx能力 
					<span style="color:#06cdcd">55分</span>
					<p>文案111文案11文案11文案11文案11</p>
				</div>
				<div>xxx能力 
					<span style="color:#06cdcd">55分</span>
					<p>文案111文案11文案11文案11文案11</p>
				</div>
				<div>xxx能力 
					<span style="color:#06cdcd">55分</span>
					<p>文案111文案11文案11文案11文案11</p>
				</div>
				<div>xxx能力 
					<span style="color:#06cdcd">55分</span>
					<p>文案111文案11文案11文案11文案11</p>
				</div>
			</div>
		</div>
		<div  class="title" id="career-match-result">
			<span >意向职业匹配结果</span>
		</div>
		<div class="container flex-container">
			<div class="career-aside-left">
				<div>你的意向职业是</div>
				<h2>数据挖掘工程师</h2>
				<div>岗位匹配度</div>
				<h2>较高</h2>
				<div style="width: 680px; height: 320px;margin-top: 30px;border-top: 1px solid #f5f5f5;padding-top: 20px ">
					<career-bar :data="[]"></career-bar>
				</div>
			</div>
			<career-card></career-card>
		</div>
		<div  class="title" style="margin-bottom: 30px;" id="career-match-evaluation">
			<span >职业特质综合评价</span>
		</div>
		<div class="container">
			<feature-table></feature-table>
		</div>
		<div  class="title" style="margin: 40px auto;" id="career-match-detail">
			<span>职位能力细分评估</span>
		</div>
		<div class="container">
			<detail-item></detail-item>
			<detail-item></detail-item>
			<detail-item></detail-item>
			<detail-item></detail-item>
		</div>	
		<div  class="title" style="margin: 40px auto;" id="career-match-other">
			<span>其他匹配职位</span>
		</div>
		<div class="container flex-container" style="justify-content: space-between">
			<career-card titleBackground="#87cfd9" title="算法工程师"></career-card>
			<career-card titleBackground="#a7cced" title="投资顾问"></career-card>
			<career-card titleBackground="#52738f" title="数据挖掘工程师"></career-card>			
		</div>
		<div style="text-align: center; margin-top:30px;">
                <el-button type="primary" @click="clickHandle">性格得分</el-button>
        </div>
		<anthor :config="config"></anthor>	
	</div>
</template>

<script>
import vRadar from '../components/radar';
import careerBar from '../components/career-bar';
import featureTable from '../components/feature-table';
import detailItem from '../components/detail-item';
import careerCard from '../components/career-card';
import anthor from '../components/anthor';
export default {
	components: {
		vRadar,
		careerBar,
		featureTable,
		detailItem,
		careerCard,
		anthor
	},
	mounted() {
		this.drawRadarChart()
	},
	data() {
		return {
			config: [
				{
					name: '职业能力雷达图',
					id: 'career-match-radar'
				},
				{
					name: '意向职业匹配结果',
					id: 'career-match-result'
				},
				{
					name: '职业特质综合评价',
					id: 'career-match-evaluation'
				},
				{
					name: '职位能力细分评估',
					id: 'career-match-detail'
				},
				{
					name: '其他匹配职位',
					id: 'career-match-other'
				}
			]
		}
	},
	methods: {
		drawRadarChart() {
			// 基于准备好的dom，初始化echarts实例
			let radarChart = this.$echarts.init(document.getElementById('radarChart'))
			radarChart.setOption({
				radar: [
					{
						indicator: [
							{text: '满足客户', max: '100'},
							{text: '管理及增长', max: '100'},
							{text: '制定销售计划', max: '100'},
							{text: '建立联系', max: '100'},
							{text: '建立购买愿望', max: '100'},
							{text: '创造解决方案', max: '100'},
							{text: '展示', max: '100'},
							{text: '成交', max: '100'},
						],
						radius: 90,
						startAngle: 90,
						splitNumber: 5,
						shape: 'circle',
						splitArea: {
							areaStyle: {
								color: ['rgba(210, 210, 210, 0.5)',
									'rgba(170, 170, 170, 0.5)', 'rgba(130, 130, 130, 0.5)',
									'rgba(90, 90, 90, 0.5)', 'rgba(50, 50, 50, 0.5)'],
								shadowColor: 'rgba(0, 0, 0, 0.3)',
								shadowBlur: 10
							}
						},
						axisLine: {
							lineStyle: {
								color: 'rgba(0, 0, 0, 0.5)'
							}
						},
						splitLine: {
							lineStyle: {
								color: 'rgba(255, 255, 255, 0.5)'
							}
						}
					}
				],
				tooltip: {},
				series: [
					{
						name: '雷达图',
						type: 'radar',
						data: [
							{
								value: [10, 85, 40, 80, 40, 60, 44, 31, 22],
								name: '图一',
								symbol: 'rect',
								symbolSize: 5,
								lineStyle: {
									color: '#000'
								},
								itemStyle: {
									color: 'red',
								},
							}
						]
					},
				]
			});
		},
		clickHandle() {
			this.$router.push({name: 'PersonalityTest'})
		}
	},
}
</script>

<style>

.feature-container {
	background: #f5f5f5;
	height: 360px;
	border-radius: 10px;
}
.flex-container {
	display: flex;
	justify-content: center;
	flex-wrap: nowrap;
	align-items: center;
}
.radar-box {
	height: 360px;
	width: 420px;
}
.right-content {
	flex: 1;
	width: 605px;
	padding: 15px;
}
.right-content > div {
	margin-bottom: 10px;
	height: 60px;
}
.right-content> div:last-child {
	margin-bottom: 0px;
} 

.career-aside-left {
	padding: 20px;
	flex: 1;
	text-align: center;
}
.career-aside-left div {
	line-height: 30px;
}
</style>





