<template>
    <div ref="radarChart"></div>
</template>

<script>

    export default {
        props: {
            data: {
                type: Array
            }
        },
        data() {
            return {
                radarChart: null,
            };
        },
        methods: {
            drawRadar() {
               //  const data = this.data.map(item => item || 0);
               /*
                if (this.radarChart) {
                    this.radarChart.dispose();
                }*/
                if (this.$refs.radarChart) {
                    const radarChart = this.$echarts.init(this.$refs.radarChart);
                    console.log(radarChart)
                    this.radarChart = radarChart;
                    radarChart.setOption({
                        radar: [
                            {
                                indicator: [
                                    {text: '满足客户', max: '100'},
                                    {text: '管理及增长', max: '100'},
                                    {text: '制定销售计划', max: '100'},
                                    {text: '建立联系', max: '100'},
                                    {text: '建立购买愿望', max: '100'},
                                    {text: '创造解决方案', max: '100'},
                                    {text: '展示', max: '100'},
                                    {text: '成交', max: '100'},
                                ],
                                radius: 90,
                                startAngle: 90,
                                splitNumber: 5,
                                shape: 'circle',
                                splitArea: {
                                    areaStyle: {
                                        color: ['rgba(210, 210, 210, 0.5)',
                                            'rgba(170, 170, 170, 0.5)', 'rgba(130, 130, 130, 0.5)',
                                            'rgba(90, 90, 90, 0.5)', 'rgba(50, 50, 50, 0.5)'],
                                        shadowColor: 'rgba(0, 0, 0, 0.3)',
                                        shadowBlur: 10
                                    }
                                },
                                axisLine: {
                                    lineStyle: {
                                        color: 'rgba(255, 255, 255, 0.5)'
                                    }
                                },
                                splitLine: {
                                    lineStyle: {
                                        color: 'rgba(255, 255, 255, 0.5)'
                                    }
                                }
                            }
                        ],
                        tooltip: {},
                        series: [
                            {
                                name: '雷达图',
                                type: 'radar',
                                data: [
                                    {
                                        value: [10, 85, 40, 80, 40, 60, 44, 31, 22],
                                        name: '图一',
                                        symbol: 'rect',
                                        symbolSize: 5,
                                        lineStyle: {
                                            color: '#444'
                                        },
                                        itemStyle: {
                                            color: 'red',
                                        },
                                    }
                                ]
                            },
                        ]
                    });
                    window.addEventListener('resize', this.resizeHandler);
                    this.$nextTick(() => {
                        this.resizeHandler();
                    });
                }
            },
            resizeHandler() {
                this.radarChart && this.radarChart.resize();
            },
        },
        created() {
            this.$nextTick(() => {
                if (Object.prototype.toString.call(this.data) === '[object Array]' && this.data.length > 0) {
                    console.log('create');
                    this.drawRadar();
                }
            });
        },
        beforeDestroy() {
            window.removeEventListener('resize', this.resizeHandler);
        },
        watch: {
            /**
             * 当传入的data改变时，重新渲染radarChart
             */
            data() {
                if (Object.prototype.toString.call(this.data) === '[object Array]' && this.data.length > 0) {
                    this.drawRadar();
                }
            }
        },
    };
</script>



